import { useState, useEffect } from 'react';

function useStats() {
  const [stats, setStats] = useState();
  function fetchApi() {
    useEffect(async () => {
      await fetch('https://covid19.mathdro.id/api').then(data => data.json());
    }, []);
  }
  fetchApi();
}

